			const head = {
				'Authorization': "Bearer " + jwt
			};
			const result = await this.$myRequest({
				method: 'GET',
				url: '/dwellers/' + this.num + '/',
				header: head,
			})
			// console.log(this.num)
			// console.log(result.data.cn_sname)
